.top-nav__header {
  height: 6rem;
  background-color: var(--color-grey-dark-1);
  border-bottom: var(--color-grey-light-2);
  display: flex;
  justify-content: space-between;
  align-items: center; }

.top-nav__logo, .top-nav__logo-container {
  display: flex;
  align-items: center;
  min-width: 0; }

.top-nav__logo-image {
  max-height: 5rem;
  margin: 0.2rem 0 0 0; }

.user-nav {
  display: flex;
  align-self: stretch;
  align-items: center;
  font-size: 1.4rem;
  letter-spacing: 0.1rem; }
  .user-nav > * {
    padding: 0 2rem;
    cursor: pointer;
    height: 100%;
    display: flex;
    align-items: center; }
  .user-nav > a:hover {
    background-color: var(--color-grey-dark-2); }
  .user-nav > a:hover .user-nav__icon {
    fill: var(--color-grey-dark-1); }
  .user-nav__icon-box {
    position: relative;
    color: inherit;
    text-decoration: inherit;
    display: flex; }
  .user-nav__icon {
    max-height: 2.25rem;
    max-width: 2.25rem;
    fill: var(--color-grey-dark-2); }
  .user-nav__label {
    margin-left: 0.75rem;
    margin-right: 0.75rem;
    min-width: 0; }
